<template>
    <div class="info-block">
        <div class="icon pull-left"><i class="fa" :class="icon"></i></div>
        <div class="wrap">
            <div class="title">{{title}}</div>
            <div class="counter">{{value}}/{{target}}</div>
            <div class="progress"><div class="load" :style="{width: persent + '%'}"></div></div>
            <div class="descr">Прогресс: {{persent}}%</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'infoBlock',
        props: {
            title: {
                type: String,
                required: true
            },
            icon: {
                type: String,
                required: true
            },
            target: {
                type: Number,
                required: true
            },
            value: {
                type: Number,
                required: true
            }
        },
        computed: {
            persent() {
                return Math.min(parseInt(this.value / this.target * 100), 100);
            }
        }
    }
</script>

<style>
    .info-block {
        height: 80px;
        background-color: #449fd4;
        color: white;
        border-radius: 3px;
        margin-bottom: 10px;
    }
    .info-block .icon {
        width: 80px;
        height: 80px;
        line-height: 80px;
        background-color: #3d90bf;
        border-radius: 3px 0 0 3px;
        text-align: center;
        font-size: 45px;
    }
    .info-block .icon .fa-user-plus {
        font-size: 40px;
        position: relative;
        top: -2px;
        left: 3px;
    }
    .info-block .icon .fa-ruble {
        font-size: 42px;
    }
    .info-block .wrap {
        margin-left: 80px;
        height: 80px;
    }
    .info-block .wrap .title {
        font-size: 14px;
        font-weight: 400;
        padding-top: 13px;
        margin-left: 10px;
    }
    .info-block .wrap .counter {
        font-size: 12px;
        margin-left: 10px;
        margin-top: 2px;
        margin-bottom: 5px;
    }
    .info-block .wrap .progress {
        background-color: #3d90bf;
        height: 2px;
    }
    .info-block .wrap .progress .load {
        transition: width 0.3s ease;
        background-color: white;
        height: 2px;
    }
    .info-block .wrap .descr {
        font-size: 12px;
        margin-left: 10px;
        margin-top: 5px;
        font-style: italic;
    }
</style>


