<template>
    <div class="row">
        <div class="col-xxl-6 col-xl-8 col-lg-8 col-md-10 hidden-zero hidden-xs hidden-sm">
            <section-block :buttons="[{name: 'Редактирование', url: '/profile/edit', icon: 'fa-edit'}]" title="Ваш профиль">
                <div class="profile-block">
                    <img :src="auth.avatar" alt="" class="img-circle">
                    <div class="fullname">{{fullname}}</div>
                </div>
                <div class="block-lines">
                    <div class="block-line">
                        Зарегистрирован <span class="pull-right">{{auth.created_at | moment('Do MMMM YYYY, hh:mm')}}</span>
                    </div>
                    <div class="block-line">
                        Группа <span class="pull-right">{{auth.group}}</span>
                    </div>
                    <div class="block-line">
                        E-mail <span class="pull-right">{{auth.email}}</span>
                    </div>
                    <div class="block-line">
                        Телефон <span class="pull-right">{{auth.phone}}</span>
                    </div>
                </div>
                <button class="btn btn-special-primary">Отправить сообщение</button>
            </section-block>
            <section-block :buttons="[{name: '', url: '/profile/edit', icon: 'fa-edit'}]" title="О Вас">
                <div class="block-multiline">
                    <div class="multiline">
                        <div class="header-multiline"><i class="fa fa-file-text fa-fw"></i> Общие сведения:</div>
                        <div class="lines">{{auth.about? auth.about : 'нет данных'}}</div>
                    </div>
                    <div class="multiline">
                        <div class="header-multiline"><i class="fa fa-map-marker fa-fw"></i> Район:</div>
                        <div class="lines">{{auth.district.name}}</div>
                    </div>
                    <div class="multiline">
                        <div class="header-multiline"><i class="fa fa-pencil fa-fw"></i> Специализации:</div>
                        <div class="lines">
                            <div class="spec" v-for="spec in auth.specialties">{{spec.name}}</div>
                        </div>
                    </div>
                </div>
            </section-block>
            <div class="row">
                <div class="col-xs-24">
                    <info-block title="Количество дней регистрации" icon="fa-clock-o" :target="30" :value="20"/>
                </div>
                <div class="col-xs-24">
                    <info-block title="Количество приглашенных" icon="fa-user-plus" :target="10" :value="5"/>
                </div>
                <div class="col-xs-24">
                    <info-block title="Стоимость добавленных товаров" icon="fa-ruble" :target="10000" :value="5000"/>
                </div>
            </div>
        </div>
        <div class="col-xxl-18 col-xl-16 col-lg-16 col-md-14 col-sm-24">
            <transition :enter-active-class="transitionPageIn" :leave-active-class="transitionPageOut" :duration="500" mode="out-in">
                <router-view></router-view>
            </transition>
        </div>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import infoBlock from '../global/info-section.vue'
    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'profileMain',
        components: {
            sectionBlock,
            infoBlock
        },
        data() {
            return {
                transitionPageIn: 'animated05 fadeIn',
                transitionPageOut: 'animated05 fadeOut'
            }
        },
        computed: {
            ...mapState(['auth', 'districts']),
            ...mapGetters(['fullname'])
        },
//        watch: {
//            '$route' (to, from) {
//                const toDepth = to.path.split('/').length
//                const fromDepth = from.path.split('/').length
//                this.transitionPageIn = 'animated05 ' + (toDepth < fromDepth ? 'fadeInLeft' : 'fadeInRight')
//                this.transitionPageOut = 'animated05 ' + (toDepth < fromDepth ? 'fadeOutRight' : 'fadeOutLeft')
//            }
//        }
    }
</script>


