<template>
    <div class="team">
        <div class="row">
            <div class="col-xxl-8 col-lg-12 col-zero-24" v-for="(user, key) in myteam">
                <section-block :title="user.firstname + ' ' + user.surname" :class="{invitedBy: !key}">
                    <div class="profile acenter">
                        <img :src="user.avatar" class="img-circle">
                        <div class="fullname">{{user.firstname + ' ' + user.surname}}</div>
                        <time class="since">Участник с {{user.created_at | moment('Do MMMM YYYY')}}</time>
                        <router-link :to="'/profile/'+user.id">
                            <button class="route btn btn-primary btn-small">Профиль</button>
                        </router-link>
                        <router-link :to="'/chat/'+user.id">
                            <button class="route btn btn-default btn-small">Написать сообщение</button>
                        </router-link>
                        <div class="confirmation" v-if="user.confirmation && user.confirmation.length">
                            <i class="fa fa-arrow-down"></i>
                            <div class="users aright" v-for="confirm in user.confirmation">
                                <router-link class="user pull-left" :to="'/profile/'+confirm.id">
                                    {{confirm.firstname + ' ' + confirm.surname}}
                                </router-link>
                                <button class="btn btn-success btn-small">Активировать</button>
                            </div>
                        </div>
                    </div>
                </section-block>
            </div>
        </div>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import { mapState } from 'vuex'

    export default {
        name: 'team',
        components: {
            sectionBlock
        },
        computed: {
            ...mapState(['myteam']),
        }
    }
</script>

<style lang="sass" src="@/assets/sass/pages/myteam.sass"></style>


